import OpenAI from 'openai';

const openai = new OpenAI({
  apiKey: process.env.NEXT_PUBLIC_OPENAI_API_KEY,
});

type Language = 'no' | 'en' | 'sv' | 'da';
type Style = 'detailed' | 'summary' | 'bullet';

export interface SummaryOptions {
  language?: Language;
  style?: Style;
}

const languageMap: Record<Language, string> = {
  'no': 'Norwegian',
  'en': 'English',
  'sv': 'Swedish',
  'da': 'Danish'
};

const styleMap: Record<Style, string> = {
  'detailed': 'a detailed paragraph',
  'summary': 'a concise summary',
  'bullet': 'bullet points'
};

export async function generateMeetingSummary(
  transcript: string,
  options: SummaryOptions = { language: 'no', style: 'summary' }
): Promise<string> {
  try {
    const language = options.language || 'no';
    const style = options.style || 'summary';

    const prompt = `Please provide ${styleMap[style]} of the following meeting transcript in ${languageMap[language]}. Focus on key decisions, action items, and important discussions:\n\n${transcript}`;

    const response = await openai.chat.completions.create({
      model: "gpt-3.5-turbo",
      messages: [
        {
          role: "system",
          content: "You are a professional meeting summarizer. Provide clear, concise, and well-structured summaries."
        },
        {
          role: "user",
          content: prompt
        }
      ],
      temperature: 0.7,
      max_tokens: 500
    });

    return response.choices[0]?.message?.content || 'Could not generate summary';
  } catch (error) {
    console.error('Error generating meeting summary:', error);
    return 'Error generating summary';
  }
} 